import React from 'react'

function ContactAgencyAgent() {
    return (
        <ul className="sasw_list mb0">
            <li className="search_area">
                <div className="form-group">
                    <input type="text" className="form-control" id="exampleInputName1e" placeholder="Your Name"/>
                </div>
            </li>
            <li className="search_area">
                <div className="form-group">
                    <input type="number" className="form-control" id="exampleInputName2dd" placeholder="Phone"/>
                </div>
            </li>
            <li className="search_area">
                <div className="form-group">
                    <input type="email" className="form-control" id="exampleInputEmail" placeholder="Email"/>
                </div>
            </li>
            <li className="search_area">
                <div className="form-group">
                    <textarea id="form_message" name="form_message" className="form-control required" rows="5" required="required" placeholder="Your Message"></textarea>
                </div>
            </li>
            <li>
                <div className="search_option_button">
                    <button type="submit" className="btn btn-block btn-thm">Search</button>
                </div>
            </li>
        </ul>
    )
}

export default ContactAgencyAgent
